@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;700&display=swap);


/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

* { box-sizing: border-box; }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body {
  background: var(--gray-1);
}
.ctnr {
  padding: 100px;
}

/* Typographic Scale: Major Third (1.250) */

/* Font Size Variables */
:root {
    --scale-factor: 1.250;
    --font-base: 14px;

    --scale-1: calc(var(--scale1) / var(--scale-factor));
    --scale1: 1em;
    --scale2: calc(var(--scale1) * var(--scale-factor));
    --scale3: calc(var(--scale2) * var(--scale-factor));
    --scale4: calc(var(--scale3) * var(--scale-factor));
    --scale5: calc(var(--scale4) * var(--scale-factor));
    --scale6: calc(var(--scale5) * var(--scale-factor));
}

body, .input { font-family: 'Quicksand', sans-serif; font-size: 14px; font-size: var(--font-base); }

/* Set default weight */
.text--small-body, .text--body, .text--big-body, .text--sub-heading, .text--heading, .text--sub-title, .text--title, .text--label {
    color: var(--gray-5);
    font-weight: 500;
}

/* Set line-heights */
.text--small-body, .text--body, .text--big-body {
    line-height: 1.6;
}
.text--sub-heading, .text--heading, .text--sub-title, .text--title , .text--label {
    line-height: 1.3;
}

/* Set font sizes */
.text--small-body, .text--label {
    font-size: calc(1em / 1.250);
    font-size: var(--scale-1);
}
.text--body, .input { 
    font-size: 1em; 
    font-size: var(--scale1);
}
.text--big-body {
    font-size: calc(1em * 1.250);
    font-size: var(--scale2);
}
.text--sub-heading {
    font-size: calc(calc(1em * 1.250) * 1.250);
    font-size: var(--scale3);
}
.text--heading {
    font-size: calc(calc(calc(1em * 1.250) * 1.250) * 1.250);
    font-size: var(--scale4);
}
.text--sub-title {
    font-size: calc(calc(calc(calc(1em * 1.250) * 1.250) * 1.250) * 1.250);
    font-size: var(--scale5);
}
.text--title {
    font-size: calc(calc(calc(calc(calc(1em * 1.250) * 1.250) * 1.250) * 1.250) * 1.250);
    font-size: var(--scale6);
}

/* Visual Utilities */
.text--label {
    font-weight: 700;
}
.text--bold, strong {
    font-weight: 700;
}
.text--italic, i {
    font-style: italic;
}
.text--strike {
    text-decoration: line-through;
}
.text--underline {
    text-decoration: underline;
}

/* Alignment Utilities */
.text--align-center {
    text-align: center;
}
.text--align-right {
    text-align: right;
}
/* Spacing Units */

:root {
    --su-5: 5px;
    --su-10: 10px;
    --su-20: 20px;
    --su-30: 30px;
    --su-50: 50px;
    --su-100: 100px;
}
/* TopNav Component */

.topNav {
    background: var(--gray-0);
    padding: 0 var(--su-50);
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
}
.topNav__menu {
    justify-content: right;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0, -webkit-max-content));
    grid-template-columns: repeat(auto-fit, minmax(0, max-content));
}
.topNav__menu__item {
    font-size: var(--scale1);
    font-weight: 400;
    color: var(--gray-4);
    padding: var(--su-30) var(--su-20);
    text-decoration: none;
    transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
}
.topNav__menu__item:hover {
    color: var(--gray-5);
    background: var(--brand-light);
}
.topNav__menu__item__active {
    color: var(--gray-5);
    background: var(--brand-light);
    border-bottom: 2px solid var(--brand);
    transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
}
/* Container Component */

/* container Settings - Affects layout */
.container {
    display: grid;
    grid-auto-rows: -webkit-max-content;
    grid-auto-rows: max-content;
}

/* Container Sizes - Affects padding */
.container--small {
    padding: var(--su-10);
}
.container--medium {
    padding: var(--su-30);
}
.container--large {
    padding: var(--su-50);
}
/* container Sizes - Affects spacing between children elements */
.container--spacing-10 {
    grid-gap: var(--su-10);
}
.container--spacing-20 {
    grid-gap: var(--su-20);
}
.container--spacing-30 {
    grid-gap: var(--su-30);
}
.container--spacing-50 {
    grid-gap: var(--su-10);
}
.container--spacing-100 {
    grid-gap: var(--su-100);
}

/* Container Layout - Affects number of columns */
.container--col-2 {
    grid-template-columns: 1fr 1fr;
}
.container--col-3 {
    grid-template-columns: 1fr 1fr 1fr;
}
.container--col-4 {
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.container--col-auto {
    grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
}

/* Container Utilities */
.container--limit-height {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}
.container--no-top-padding{
    padding-top: 0;
}
/* MediaObject Settings */
.media-object {
    display: grid;
    grid-template-columns: 1fr 5fr;
}

/* MediaObject Vertical Alignment - Affects vertical alignment of children */
.media-object--align-top {
    align-items: start;
}
.media-object--align-center {
    align-items: center;
}
.media-object--align-bottom {
    align-items: end;
}

/* MediaObject Order */
.media-object--inverse > *:nth-child(1) {
    grid-column: 2;
}
.media-object--inverse > *:nth-child(2) {
    grid-column: 1;
}
.media-object--inverse > *:nth-child(1), .media-object--inverse > *:nth-child(2) {
    grid-row: 1;
}

/* MediaObject Sizes - Affects spacing between children elements */
.media-object--spacing-10 {
    grid-gap: var(--su-10);
}
.media-object--spacing-20 {
    grid-gap: var(--su-20);
}
.media-object--spacing-30 {
    grid-gap: var(--su-30);
}
.media-object--spacing-50 {
    grid-gap: var(--su-10);
}
.media-object--spacing-100 {
    grid-gap: var(--su-100);
}
/* Card Component */

/* Card Settings - Affects layout */
.card {
    border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    -webkit-border-radius: 6px;
    overflow: hidden;
}

/* Card Types - Affects look and feel */
.card--box {
    background: var(--gray-0);
    box-shadow: 0px 4px 8px var(--gray-2);
}
.card--border {
    background: transparent;
    border: 1px solid var(--gray-3);
}

/* Card Elements */
.card__header { 
    border-bottom: 1px solid var(--gray-3);
    padding-top: 15px;
    padding-bottom: 15px; 
}
.card__header--small {
    padding-left: 10px;
    padding-right: 10px;
}
.card__header--medium {
    padding-left: 30px;
    padding-right: 30px;
}
.card__header--large {
    padding-left: 50px;
    padding-right: 50px;
}
/* Button Component */

/* Button Settings - Affects layout */
.button {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
    justify-content: center;
    grid-gap: 5px;
    line-height: 16px;
    border: 1px solid transparent;
    outline: 0;
    border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    -webkit-border-radius: 3px;
    transition: all 0.2s ease-in, border-color 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in, border-color 0.2s ease-in;
    -moz-transition: all 0.2s ease-in, border-color 0.2s ease-in;
    -ms-transition: all 0.2s ease-in, border-color 0.2s ease-in;
    -o-transition: all 0.2s ease-in, border-color 0.2s ease-in;
}
.button:hover {
    cursor: pointer;
}

/* Button Types - Affects look and feel */
.button--primary {
    background: var(--brand);
    border-color: var(--brand);
    color: var(--gray-0);
    font-weight: 900;
}
.button--primary:hover {
    background: var(--brand-dark);
    border-color: var(--brand-dark);
    box-shadow: 0 0 0 4px var(--brand-light);
}

.button--secondary {
    background: transparent;
    color: var(--gray-5);
    border: 1px solid var(--gray-3);
}
.button--secondary:hover {
    border-color: var(--brand);
    box-shadow: 0 0 0 4px var(--brand-light);
}
.button--link {
    background: transparent;
    padding: 0;
    font-weight: 900;
    color: var(--brand);
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.button--link:hover {
    color: var(--brand-dark);
}
.button--link.button--small, .button--link.button--medium, .button--link.button.large {
    padding: 0;
}
.button--primary:disabled, .button--secondary:disabled {
    background: var(--gray-2);
    border-color: var(--gray-3);
    color: var(--gray-4);
}
.button:disabled:hover {
    box-shadow: inherit;
    cursor: not-allowed;
}

/* Button Sizes - Affects padding and font size */
.button--small {
    padding: 6px;
    font-size: var(--scale-1);
}
.button--medium {
    padding: 11px;
    font-size: var(--scale1);
}
.button--large {
    padding: 16px;
    font-size: var(--scale2);
}
/* Image Component */

.image {
    width: 100%;
    height: auto;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}
/* Avatar Component */

/* Avatar Settings */
.avatar {
    height: auto;
    border-radius: 128px;
    -webkit-border-radius: 128px;
    -moz-border-radius: 128px;
    -ms-border-radius: 128px;
    -o-border-radius: 128px;
    overflow: hidden;
}
/* Form Components */

.form-field {
    display: grid;
    grid-gap: var(--su-5);
}
.input {
    font-size: var(--scale1);
    line-height: 16px;
    color: var(--gray-5);
    background-color: var(--gray-0);
    border: 1px solid var(--gray-3);
    outline: 0;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
}
.input:hover, .input:focus {
    border-color: var(--brand);
}
.input:focus {
    box-shadow: 0 0 0 4px var(--brand-light);
}
.input:disabled {
    color: var(--gray-4);
    background-color: var(--gray-1);
    border-color: var(--gray-2);
    cursor: not-allowed;
}
.input.has-error {
    background-color: var(--error-bg);
    border-color: var(--error);
}
.input.has-error:focus {
    background-color: var(--gray-0);
}
.input::-webkit-input-placeholder {
    color: var(--gray-4);
}
.input:-ms-input-placeholder {
    color: var(--gray-4);
}
.input::-ms-input-placeholder {
    color: var(--gray-4);
}
.input::placeholder {
    color: var(--gray-4);
}

/* Input Sizes - Affects padding and font size */
.input--small {
    padding: 6px;
    font-size: var(--scale-1);
}
.input--medium {
    padding: 11px;
    font-size: var(--scale1);
}
.input--large {
    padding: 13px;
    font-size: var(--scale2);
}

/* Select Styling */
.input--select {
    overflow: visible;
    line-height: 22px;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 1L5 5L1 1' stroke='%23002148' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: calc(100% - 10px) center;
}
.input--select:hover {
    cursor: pointer;
}
.input--select:disabled {
    background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 1L5 5L1 1' stroke='%23002148' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: calc(100% - 10px) center;
    cursor: not-allowed;
}

/* Input Number settings */
.form-field div {
    display: grid;
    grid-template-columns: 40px 1fr 40px;
}
.input--number {
    grid-column: 1 / 4;
    grid-row: 1;
    text-align: center;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}
.input--number__minus { 
    grid-column: 1 / 2;
}
.input--number__plus { 
    grid-column: 3 / 4;
}
.input--number__minus, .input--number__plus {
    z-index: 2;
    grid-gap: 0;
    grid-row: 1;
    display: grid;
    align-items: center;
    justify-items: center;
}

/* Checkbox Settings */
.checkbox, .radio-button {
    position: relative;
    display: grid;
    align-items: center;
    grid-gap: var(--su-10);
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    height: 20px;
    padding-left: var(--su-30);
    font-size: var(--scale1);
    line-height: 16px;
    color: var(--gray-5);
}
.checkbox input, .radio-button input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    width: 20px;
    height: 20px;
}
.checkbox__checkmark, .radio-button__checkmark {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    background-color: var(--gray-0);
    border: 1px solid var(--gray-3);
    outline: 0;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
}
.checkbox input:hover ~ .checkbox__checkmark, .radio-button input:hover ~ .radio-button__checkmark {
    border-color: var(--brand);
    box-shadow: 0 0 0 4px var(--brand-light);
}
.checkbox input:checked ~ .checkbox__checkmark, .radio-button input:checked ~ .radio-button__checkmark {
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.60227L4.63636 8.23864L11 1.875' stroke='white' stroke-width='1.5' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: center center;
    background-color: var(--brand);
    border-color: var(--brand);
}
.checkbox input:disabled ~ .checkbox__checkmark, .radio-button input:disabled ~ .radio-button__checkmark {
    background: var(--gray-2);
    border-color: var(--gray-3);
    color: var(--gray-4);
}
.checkbox input:disabled:hover ~ .checkbox__checkmark, .radio-button input:disabled:hover ~ .radio-button__checkmark {
    border-color: var(--gray-3);
    box-shadow: none;
    cursor: not-allowed;
}
.checkbox__checkmark.has-error, .radio-button__checkmark.has-error {
    background-color: var(--error-bg);
    border-color: var(--error);
}
.radio-button__checkmark {
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
.radio-button input:checked ~ .radio-button__checkmark {
    background-image: none;
    background: var(--gray-0);
    box-shadow: inset 0 0 0 4px var(--brand);
}
.radio-button input:checked:hover ~ .radio-button__checkmark {
    box-shadow: 0 0 0 4px var(--brand-light), inset 0 0 0 4px var(--brand);
}
